.wrapper {
  background-color: white;

  width: 500px;
  height: 100vh;

  padding: 50px;

  display: flex;
  flex-direction: column;

  justify-content: center;
}

.imgWrapper {
  width: calc(100vw - 500px);
  height: 100vh;
}

.img {
  width: 100%;
  height: 100%;

  object-fit: cover;
}
